import React, {Fragment} from 'react'
import {CryptoDetails} from './CryptoDetails'
import {sortBy, prop} from 'ramda'
import {ListScreen} from '../dumb/styledComponent/list'
import PropTypes from 'prop-types'

export const List = ({cryptos, getMore, loadingMore}) => (
  <Fragment>
    <ListScreen>
      {cryptos && cryptos.length > 0 ? (
        sortBy(prop('rank'), cryptos).map(crypto => (
          <CryptoDetails key={crypto.id} crypto={crypto} />
        ))
      ) : (
        <p>No currency</p>
      )}
    </ListScreen>
  </Fragment>
)

List.propTypes = {
  cryptos: PropTypes.arrayOf(PropTypes.object),
  getMore: PropTypes.func,
  loadingMore: PropTypes.bool,
}



