import React, {Component, Fragment} from 'react'
import axios from 'axios'
import {List} from './List'
import {
  Container,
  SubHeader,
  Search,
  Row,
  RefreshButton,
} from '../dumb/styledComponent/dashboard'
import {TextNum, Loader} from '../dumb/styledComponent/shared'
import {CircleLoader} from 'react-spinners'
import {SERVER} from '../../constants/url'

export class DashBoard extends Component {
  state = {
    cryptos: [],
    metadata: {},
    loading: false,
    loadingRefresh: false,
    numberOfCurrency: 30,
    value: '',
  }

  callAxios = ({loader} = {loader: 'loading'}) => {
    this.setState({[loader]: true})
    axios
      .get(`${SERVER}/cryptos`)
      .then(res =>
        this.setState({
          cryptos: res.data.cryptos,
          metadata: res.data.metadata,
          [loader]: false,
        })
      )
      .catch(() => this.setState({loading: false}))
  }

  refresh = () => {
    this.callAxios({
      loader: 'loadingRefresh',
    })
  }

  getData = () => {
    this.callAxios({loader: 'loading'})
  }

  handleInput = e => {
    const value = e.target.value
    this.setState({value: value})
  }

  getFilterdCryptos = () =>
    this.state.cryptos.filter(
      crypto =>
        crypto.name
          .trim()
          .toLowerCase()
          .search(this.state.value.trim().toLowerCase()) !== -1
    )
  componentDidMount() {
    this.getData()
    const interval = setInterval(() => this.refresh(), 300000)
    this.setState({interval})
  }
  componentWillUnmount() {
    const {interval} = this.state
    interval && clearInterval(interval)
  }
  render() {
    const {loading, loadingRefresh, metadata} = this.state
    const filteredCryptos = this.getFilterdCryptos()
    return (
      <Fragment>
        <Container>
          <SubHeader>
            <Row>
              <RefreshButton onClick={this.refresh}>Refresh</RefreshButton>
              <TextNum>
                {filteredCryptos && filteredCryptos.length} /{' '}
                {metadata.num_cryptocurrencies}{' '}
              </TextNum>
              {loadingRefresh && (
                <CircleLoader color={'#673ab7'} loading={loadingRefresh} />
              )}
            </Row>
            <Search
              type="text"
              placeholder="Search"
              onChange={this.handleInput}
            />
          </SubHeader>

          {loading ? (
            <Loader>
              <CircleLoader color={'#673ab7'} loading={loading} />
            </Loader>
          ) : (
            <List cryptos={filteredCryptos} />
          )}
        </Container>
      </Fragment>
    )
  }
}



