import React, {Component} from 'react'
import {MatchType, CryptoType} from '../../constants/types'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import {removeHistory} from '../../actions/wallet'
import {DateTime} from 'luxon'
import {getWalletCryptoNbr} from '../../utils/functions'
import {CryptoProvider} from '../providers/CryptoProvider'
import {Link} from 'react-router-dom'

const mapStateToProps = state => ({
  historyData: state.wallet,
})

@connect(mapStateToProps)
export class SellingPage extends Component {
  static propTypes = {
    match: MatchType,
    dispatch: PropTypes.func,
    historyData: PropTypes.arrayOf(CryptoType),
  }
  state = {
    quantity: 1,
  }

  SellCryptos = ({crypto, rate, quantity, date, cryptoId}) => {
    this.props.dispatch(
      removeHistory({
        cryptoId,
        crypto,
        rate,
        quantity,
        date,
      })
    )
  }

  render() {
    const {
      historyData,
      match: {
        params: {id},
      },
    } = this.props
    const {quantity} = this.state
    return (
      <CryptoProvider id={id}>
        {({crypto, loading, error}) => (
          <div>
            {!error && !loading ? (
              <div>
                <Link to="/wallet">Go back to your wallet</Link>
                <h2>Sell your {crypto.name}</h2>
                <p>
                  You have{' '}
                  {getWalletCryptoNbr({historyData, name: crypto.name})}{' '}
                  {crypto.name} at the moment{' '}
                </p>
                <h3>What is the rate</h3>
                <h4>Rate : ${crypto.quotes.USD.price}</h4>
                <p>Last hour : {crypto.quotes.USD.percent_change_1h} %</p>
                <p>last day : {crypto.quotes.USD.percent_change_24h} %</p>
                <p>last week : {crypto.quotes.USD.percent_change_7d} %</p>
                <input
                  type="number"
                  value={quantity}
                  onChange={e => this.setState({quantity: e.target.value})}
                />

                <button
                  onClick={() =>
                    this.SellCryptos({
                      crypto: crypto.name,
                      rate: crypto.quotes.USD.price,
                      quantity: parseInt(quantity, 10),
                      date: DateTime.local().toMillis(),
                      cryptoId: crypto.id,
                    })
                  }
                >
                  Sell
                </button>
              </div>
            ) : (
              <p>Loading</p>
            )}
          </div>
        )}
      </CryptoProvider>
    )
  }
}



