import React, {Component} from 'react'
import {MatchType, HistoryType, CryptoType} from '../../constants/types'
import {connect} from 'react-redux'
import {addHistory} from '../../actions/wallet'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
import {DateTime} from 'luxon'
import {getWalletCryptoNbr} from '../../utils/functions'
import {CryptoProvider} from '../providers/CryptoProvider'

const mapStateToProps = state => ({
  historyData: state.wallet,
})

@connect(mapStateToProps)
export class BuyingPage extends Component {
  static propTypes = {
    match: MatchType,
    dispatch: PropTypes.func,
    history: HistoryType,
    historyData: PropTypes.arrayOf(CryptoType),
  }
  state = {
    quantity: 1,
  }

  buyCryptos = ({crypto, rate, quantity, date, cryptoId}) => {
    this.props.dispatch(
      addHistory({
        cryptoId,
        crypto,
        rate,
        quantity,
        date,
      })
    )
  }

  render() {
    const {quantity} = this.state
    const {
      history: {goBack},
      historyData,
      match: {
        params: {id},
      },
    } = this.props
    return (
      <CryptoProvider id={id}>
        {({crypto, loading, error}) => (
          <div>
            {!error && !loading ? (
              <div>
                <button onClick={goBack}>Go Back to the DashBoard</button>
                <Link to="/wallet">Go to your wallet</Link>
                <h2>Buy {crypto.name}</h2>
                <h4>Rate : ${crypto.quotes.USD.price}</h4>
                <h4>Quantity : </h4>
                <input
                  type="number"
                  value={quantity}
                  onChange={e => this.setState({quantity: e.target.value})}
                />
                <button
                  onClick={() =>
                    this.buyCryptos({
                      crypto: crypto.name,
                      rate: crypto.quotes.USD.price,
                      quantity: parseInt(quantity, 10),
                      date: DateTime.local().toMillis(),
                      cryptoId: crypto.id,
                    })
                  }
                >
                  Buy
                </button>

                <p>{`You have ${
                  getWalletCryptoNbr({historyData, name: crypto.name})
                    ? getWalletCryptoNbr({historyData, name: crypto.name})
                    : 'no'
                } ${crypto.name} `}</p>
                {}
              </div>
            ) : (
              <p>Loading ...</p>
            )}
          </div>
        )}
      </CryptoProvider>
    )
  }
}



