export const ADD_HISTORY = 'ADD_HISTORY'
export const REMOVE_HISTORY = 'REMOVE_HISTORY'
export const BUY = 'buy'
export const SELL = 'sell'
const reducer = {
  [ADD_HISTORY]: (state, payload) => [...state, {...payload, type: BUY}],
  [REMOVE_HISTORY]: (state, payload) => [...state, {...payload, type: SELL}],
}

export const wallet = (state = [], action) => {
  const handler = reducer[action.type]
  return handler ? handler(state, action.payload) : state
}



