import React, {Component} from 'react'
import {DashBoard} from './components/crypto-list/DashBoard'
import {Header} from './components/dumb/Header'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import {WalletDashBoard} from './components/wallet/WalletDashBoard'
import {createStore} from 'redux'
import {Provider} from 'react-redux'
import rootReducer from './reducers/index'
import {BuyingPage} from './components/wallet/BuyingPage'
import {SellingPage} from './components/wallet/SellingPage'
import {persistStore, persistReducer} from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import {PersistGate} from 'redux-persist/integration/react'

const persistConfig = {
  key: 'root',
  storage,
}

const persistedReducer = persistReducer(persistConfig, rootReducer)

const store = createStore(
  persistedReducer,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
)
const persistor = persistStore(store)

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <PersistGate loading={<p>Loading...</p>} persistor={persistor}>
          <Router>
            <div>
              <Header />
              <Switch>
                <Route exact path="/" component={DashBoard} />
                <Route path="/wallet" component={WalletDashBoard} />
                <Route path="/crypto/:id" component={BuyingPage} />
                <Route path="/sell/:id" component={SellingPage} />
              </Switch>
            </div>
          </Router>
        </PersistGate>
      </Provider>
    )
  }
}

export default App



