import React from 'react'
import {
  HeaderStyled,
  Title,
  SubTitle,
  LinkBox,
  StyledLink,
} from './styledComponent/header'
import {NavLink} from 'react-router-dom'

export const Header = () => (
  <HeaderStyled>
    <div>
      <NavLink to="/">
        <Title>HackCoin</Title>
      </NavLink>
      <SubTitle>Hello there! General Kenobi</SubTitle>
    </div>
    <LinkBox>
      <StyledLink to="/">Buy</StyledLink>
      <StyledLink to="/wallet">Your wallet</StyledLink>
    </LinkBox>
  </HeaderStyled>
)



