import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import {CryptoType} from '../../constants/types'
import {DateTime} from 'luxon'
import {BUY} from '../../reducers/wallet'
import {
  Container,
  Title,
  SubTitle,
  Table,
  SellLink,
} from '../dumb/styledComponent/walletDashBoard'

const mapStateToProps = state => ({
  historyData: state.wallet,
})

@connect(mapStateToProps)
export class WalletDashBoard extends Component {
  static propTypes = {
    historyData: PropTypes.arrayOf(CryptoType),
  }
  getCurrency = historyData =>
    historyData.reduce(
      (acc, current) =>
        acc[current.crypto]
          ? {
              ...acc,
              [current.crypto]: {
                ...acc[current.crypto],
                quantity:
                  current.type === BUY
                    ? acc[current.crypto].quantity + current.quantity
                    : acc[current.crypto].quantity - current.quantity,
              },
            }
          : {
              ...acc,
              [current.crypto]: {
                quantity: current.quantity,
                id: current.cryptoId,
              },
            },
      {}
    )

  render() {
    const {historyData} = this.props
    const currencies = this.getCurrency(historyData)
    return (
      <Container>
        <Title>Your Wallet</Title>
        <SubTitle>Your Currencies </SubTitle>
        <Table>
          <thead>
            <tr>
              <td>Name</td>
              <td>Quantity</td>
              <td />
            </tr>
          </thead>
          <tbody>
            {Object.entries(currencies).map(currency => (
              <tr key={currency[1].id}>
                <td>{currency[0]}</td>
                <td>{currency[1].quantity}</td>
                <td>
                  {currency[1].quantity !== 0 && (
                    <SellLink to={`/sell/${currency[1].id}`}>Sell</SellLink>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </Table>

        <SubTitle>Your History</SubTitle>
        <Table>
          <thead>
            <tr>
              <td>Name</td>
              <td>Rate</td>
              <td>Quantity</td>
              <td>Total</td>
              <td>Operation</td>
              <td>Date</td>
            </tr>
          </thead>
          <tbody>
            {historyData.map(crypto => (
              <tr key={`${crypto.crypto} ${crypto.date}`}>
                <td>{crypto.crypto}</td>
                <td>{crypto.rate}</td>
                <td>{crypto.quantity}</td>
                <td>{(crypto.rate * crypto.quantity).toFixed(2)}</td>
                <td>{crypto.type === BUY ? 'Buying' : 'Selling'}</td>
                <td>
                  {DateTime.fromMillis(crypto.date).toLocaleString(
                    DateTime.DATETIME_FULL
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Container>
    )
  }
}



